.sudoku-board {
    display: grid;
    grid-template-columns: repeat(9, 40px);
    grid-template-rows: repeat(9, 40px);
    gap: 2px;
    margin-left: 500px;
    border: #000;
  }
  
  .sudoku-row {
    display: contents;
  }
  
  .sudoku-cell {
    width: 40px;
    height: 40px;
    text-align: center;
    font-size: 18px;
    border: 1px solid #ed1313;
  }
  
  .sudoku-cell:nth-of-type(3n),
  .sudoku-row:nth-of-type(3n) .sudoku-cell {
    border-right: 1px solid #000;
    border-bottom: 1px solid #000;
    border-left: 1px solid #000;
  
  }